import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler} from '@farris/devkit';
import { FuncgroupDataService } from '../../domain/role-detail.dataservice';


@Injectable()
@NgCommandHandler({
  commandName: 'save'
})
class SaveHandler extends CommandHandler {

  constructor(
    private dataService: FuncgroupDataService
  ) {
    super();
  }

  schedule() {
    this.addTask('save', () => {
      this.dataService.save();
    });
  }

}
export { SaveHandler };



