import { Entity, NgField } from '@farris/devkit';


export class GSPRoleEntity extends Entity {
    /**
   * Id
   */
  @NgField(true)
  id: string;
  /**
   * 编号
   */
  @NgField()
  code: string;

    /**
   * 名称
   */
  @NgField()
  name: string;
    /**
   * 类别
   */
  @NgField()
  roleGroup: string;
    /**
   * 描述
   */
  @NgField()
  note: string;
      /**
   * 截止开始
   */
  @NgField()
  validFrom: string;


  /**
   * 截止结束
   */
  @NgField()
  validTo: string;

  /**
   * 状态
   */
  @NgField()
  state: string;
}



