
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { FuncgroupDataService } from '../../domain/index';
import { FuncgroupStateMachine } from '../role-detail.statemachine';
import { GSPRoleDetailUIState } from '../role-detail.uistate';

@Injectable()
@NgCommandHandler({
  commandName: 'formLoad'
})
export class FormLoadHandler extends CommandHandler {

  constructor(
    private dataService: FuncgroupDataService,
    private uiState: GSPRoleDetailUIState,
    private stateMachine: FuncgroupStateMachine,
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('loadData', () => {
      const dataId = this.uiState.dataId;
      if (dataId === 'new') {
        this.stateMachine['add']();
        this.dataService.loadNewData();
      } else {
        this.stateMachine['edit']();
        this.dataService.loadData(dataId);
      }
    });
  }
}



