import { Entity, NgField } from '@farris/devkit';

/**
 * 岗位关系实体
 */
export class GSPPositionEntity extends Entity {

  /**
   * 岗位内码
   */
  @NgField(true)
  id: string;

  /**
   * 岗位编号
   */
  @NgField()
  code: string;

  /**
   * 岗位名称
   */
  @NgField()
  name: string;

}



