import { Injectable } from '@angular/core';
import { FuncgroupTreeDatastorage } from './role-list.datastorage';
import { RestfulService } from '@farris/devkit';
import { SysManagerUtil } from '../../../common';

@Injectable()
export class FuncgroupService {
  constructor(
    private rest: RestfulService,
    private datastorage: FuncgroupTreeDatastorage,
    private sessionmanager: SysManagerUtil,
  ) { }

  private entities: any;

  getdata (): any {
    const url = this.sessionmanager.roleWebApi;
    this.rest.get(url + '/all', null, this.sessionmanager.getHeader()).subscribe((result: any) => {
      console.log(result);
      this.datastorage.load(result);
    });
  }


  /**
   * 删除并重新加载
   */
  remove (id: string[]) {
    const url = this.sessionmanager.roleWebApi;
    this.rest.delete(url + '/' + id, null, this.sessionmanager.getHeader()).subscribe(() => {
      this.getdata();
      alert('删除成功');
    });
  }
}



