import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel, NgCommand } from '@farris/devkit';

import { FuncgroupTreeBindingdata } from './role-list.bindingdata';
import { FuncgroupTreeForm } from './role-list.form';
import { FuncgroupTreeStatemachine } from './role-list.statemachine';
import { FuncgroupTreeUistate } from './role-list.uistate';

/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: FuncgroupTreeBindingdata,
  form: FuncgroupTreeForm,
  stateMachine: FuncgroupTreeStatemachine,
})
class FuncgroupTreeViewmodel extends ViewModel { 

  public uiState: FuncgroupTreeUistate;
  public stateMachine: FuncgroupTreeStatemachine;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(FuncgroupTreeUistate);
  }

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'role-list-formLoad'
  })
  public formLoad() { }

  /**
   * 新增
   */
  @NgCommand({
    name: 'route',
    params: {
      action: 'add'
    }
  })
  public add() { }

  /**
   * 编辑
   */
  @NgCommand({
    name: 'route',
    params: {
      action: 'edit'
    }
  })
  public edit() { }

  /**
   * 关闭
   */
  @NgCommand({
    name: 'close',
  })
  public close() { }

  /**
   * 删除
   */
  @NgCommand({
    name: 'remove',
  })
  public remove() { }



  /**
   * 分配功能权限
   */
  @NgCommand({
    name: 'route',
    params: {
      action: 'assignOp'
    }
  })
  public assignOp() { }


}

export { FuncgroupTreeViewmodel };



