import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { FuncgroupDataStorage } from './role-detail.datastorage';
import { FuncgroupRepository } from './role-detail.repository';
import { RestfulService } from '@farris/devkit';
import { SysManagerUtil } from '../../../common';
import { FuncgroupStateMachine } from '../app/role-detail.statemachine';
import { GSPRoleEntity } from './entities/role.entity';
import { GSPRoleDetailUIState } from '../app/role-detail.uistate';
import { GSPPosRoleService } from '../position-role/domain';



@Injectable()
export class FuncgroupDataService {
  constructor(
    private rest: RestfulService,
    private fungroupDetailStore: FuncgroupDataStorage,
    private roleRep: FuncgroupRepository,
    private sessionmanager: SysManagerUtil,
    private roleStateMachine: FuncgroupStateMachine,
    private posRoleUIState: GSPRoleDetailUIState,
    private posRoleService: GSPPosRoleService
  ) {
  }
  private entity: any;
  /**
   * 加载数据
   */
  loadData(dataId: string) {
    const url = this.sessionmanager.roleWebApi;
    this.rest.get(url + '/' + dataId, null, this.sessionmanager.getHeader()).subscribe((result: any) => {
      this.fungroupDetailStore.load(result);
      console.log(result);
    });
  }

  /**
   * 保存
   */
  save() {
    const newRole = this.roleRep.getAllEntities()[0];
    newRole.roleGroup = 'admin';
    if (this.roleStateMachine.context.state === 'addState') {
      this.saveAddRole(newRole);
    } else {
      this.saveEditRole(newRole);
    }
  }

  saveAddRole(role: GSPRoleEntity) {
    const roleUrl = this.sessionmanager.roleWebApi;
    const headers = this.sessionmanager.getHeader();
    this.rest.post(roleUrl, role.data, null, headers)
      .subscribe(
        (result: any) => {
          // 这个地方必须要重新load一下，否则再去编辑的时候一些字段没有值服务端校验出错
          this.loadData(result);
          // 需要同步岗位和功能组
          if (this.posRoleUIState.needSavePosRole) {
            this.posRoleService.modifyPosByRole()
              .subscribe(() => { });
          }
          // 保存后应该走编辑状态
          this.roleStateMachine['edit']();
          alert('功能组保存成功');
      }, (error: HttpErrorResponse) => {
        console.log(error);
        alert(error.error.Message);
      }
      );
  }

  saveEditRole(role: GSPRoleEntity) {
    const roleUrl = this.sessionmanager.roleWebApi;
    const headers = this.sessionmanager.getHeader();
    this.rest.put(roleUrl, role.data, null, headers)
      .subscribe(
        result => {
          if (this.posRoleUIState.needSavePosRole) {
            this.posRoleService.modifyPosByRole()
              .subscribe(() => { });
          }
          // 保存后应该走编辑状态
          this.roleStateMachine['edit']();
          alert('功能组保存成功');
        }, (error: HttpErrorResponse) => {
          console.log(error);
          alert(error.error.Message);
        }
      );
  }
  /**
  * 新增
  */
  loadNewData() {
    this.fungroupDetailStore.clear();
    const url = this.sessionmanager.roleWebApi;
    this.rest.get(url + '/new', null, this.sessionmanager.getHeader()).subscribe((result: any) => {
      this.posRoleUIState.dataId = result[0]['id'];
      this.fungroupDetailStore.load(result);
    });
  }
}




