import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { FuncgroupBindingData} from './role-detail.bindingdata';


@Injectable()
@NgForm({
  bindingData: FuncgroupBindingData
})
class BaseinfoForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

    /**
   * 编号
   */
  @NgFormControl({
    name: 'id',
    binding: 'id'
  })
  id: FormControl;

  /**
   * 编号
   */
  @NgFormControl({
    name: 'code',
    binding: 'code'
  })
  code: FormControl;

  /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'name'
  })
  name: FormControl;


  /**
   * 分类
   */
  @NgFormControl({
    name: 'roleGroup',
    binding: 'roleGroup'
  })
  roleGroup: FormControl;

  /**
   * 描述
   */
  @NgFormControl({
    name: 'note',
    binding: 'note'
  })
  note: FormControl;

   /**
   * 限制开始时间
   */
  @NgFormControl({
    name: 'validFrom',
    binding: 'validFrom'
  })
  validFrom: FormControl;

    /**
   * 限制截止时间
   */
  @NgFormControl({
    name: 'validTo',
    binding: 'validTo'
  })
  validTo: FormControl;

    /**
   * 状态
   */
  @NgFormControl({
    name: 'state',
    binding: 'state'
  })
  state: FormControl;
}


@Injectable()
@NgForm({
  bindingData: FuncgroupBindingData
})
class LimitinfoForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }
    /**
   * 是否停用
   */
  @NgFormControl({
    name: 'isstop',
    binding: 'Isstop'
  })
  isstop: FormControl;

    /**
   * 是否不限制
   */
  @NgFormControl({
    name: 'isnolimit',
    binding: 'Isnolimit'
  })
  isnolimit: FormControl;

}


@Injectable()
@NgForm({
  bindingData: FuncgroupBindingData
})
class AuthorizationForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

     /**
   * 序号
   */
  @NgFormControl({
    name: 'id',
    binding: 'Id'
  })
  id: FormControl;

     /**
   * 编号
   */
  @NgFormControl({
    name: 'code',
    binding: 'Code'
  })
  code: FormControl;

     /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'Name'
  })
  name: FormControl;
}


export { BaseinfoForm, LimitinfoForm, AuthorizationForm };



