import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPPosRoleService } from '../../domain';

/**
 * 功能组岗位详情页面保存
 */
@Injectable()
@NgCommandHandler({
    commandName: 'savePosRole'
})
export class SavePosRoleHandler extends CommandHandler {

    constructor (
        private posRoleService: GSPPosRoleService) {
        super();
    }

    schedule () {
        this.addTask('save', () => {
            this.posRoleService.modifyPosByRole().subscribe(
                result => {
                    alert('功能组授权岗位保存成功');
                },
                error => {
                    console.log(error);
                    console.log('功能组授权岗位失败');
                }
            );
        });
    }
}



