import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { RoleListEntity, FuncgroupTreeRepository } from '../domain/index';

@Injectable()
@NgBindingData({
  entity:     RoleListEntity,
  repository: FuncgroupTreeRepository
})
class FuncgroupTreeBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { FuncgroupTreeBindingdata };




