import { Entity, NgField } from '@farris/devkit';

/**
 * 功能组列表实体
 */
export class RoleListEntity extends Entity {

  /**
   * Id
   */
  @NgField({
    primary: true
  })
  id: string;

  /**
   * code
   */
  @NgField()
  code: string;

  /**
   * name
   */
  @NgField()
  name: string;

  /**
   * 分类
   */
  @NgField()
  roleGroup: string;

  //   /**
  //  * 系统预置
  //  */
  // @NgField({
  //   dataField:'sysinit',
  // })
  // sysinit: string;

  /**
   * 创建者
   */
  @NgField()
  creatorName: string;
    /**
   * 描述
   */
  @NgField()
  note: string;

}



