import { Injectable } from '@angular/core';

/**
 * 功能组岗位组件UI状态
 */
@Injectable()
export class GSPPosRoleUIState {

    curRoleId: string;
    /** 选择的岗位列表，用于在界面上删除时的单行选择*/
    selectPosList: string[] = [];
    /**第一次岗位列表备份 */
    posIdBackUpList: string[] = [];
    /**当前界面上岗位内码 */
    curPosIdList: string[] = [];
    /** 设置岗位功能组帮助的状态值*/
    posRoleHelpFlag = false;
}




