import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';

import { FuncgroupBindingData } from './role-detail.bindingdata';
import { BaseinfoForm } from './role-detail.form';
import { FuncgroupStateMachine } from './role-detail.statemachine';
import { GSPRoleDetailUIState } from './role-detail.uistate';

/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: FuncgroupBindingData,
  form: BaseinfoForm,
  stateMachine: FuncgroupStateMachine,
})
class FuncgroupViewModel extends ViewModel {

  public uiState: GSPRoleDetailUIState;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(GSPRoleDetailUIState);
  }

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'formLoad'
   })
  public formLoad() {}

  /**
   * 保存
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

  /**
   * 关闭
   */
  @NgCommand({
    name: 'close'
   })
  public close() {}
}

export { FuncgroupViewModel };



