import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { FuncgroupViewModel } from './app/index';
import { PosRoleComponent } from './position-role/pos-role.component';

@Component({
  selector: 'app-role-detail',
  templateUrl: './role-detail.component.html',
  styleUrls: ['./role-detail.component.css']
})
export class FuncgroupComponent implements OnInit {
  @ViewChild(PosRoleComponent)
  private posRoleChild: PosRoleComponent;

  constructor(
    public viewModel: FuncgroupViewModel,
    private route: ActivatedRoute
  ) {
  }
  ngOnInit() {
    this.route.paramMap.subscribe((params: Params) => {
      this.viewModel.uiState.dataId = params.get('id');
      this.viewModel.uiState.action = params.get('action');
      this.viewModel.formLoad();
    });
  }

  saveRole () {
    // 复原
    this.viewModel.uiState.needSavePosRole = false;
    // 保存岗位和功能组标志
    if (this.posRoleChild) {
      this.viewModel.uiState.needSavePosRole = true;
    }
    this.viewModel.save();
  }
}



