import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { FuncgroupService } from '../../domain/role-list.service';
import { FuncgroupTreeUistate } from '../role-list.uistate';

@Injectable()
@NgCommandHandler({
  commandName: 'remove'
})
class RemoveHandler extends CommandHandler {

  constructor(
    private dataService: FuncgroupService,
    private uiState: FuncgroupTreeUistate,
  ) {
    super();
  }

  schedule() {
    // 删除选中
    this.addTask('remove', () => {
      const dataIds = this.uiState.selectedIds;
      this.uiState.selectedIds = [];
      if (dataIds.length === 0) {
        alert('请选择要删除的功能组！');
      } else {
        this.dataService.remove(dataIds);
      }
    });
  }
}

export { RemoveHandler };



