import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { isNullOrUndefined } from 'util';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';

import { Router } from '@angular/router';
import { ActivatedRoute, Params } from '@angular/router';

import { ResultPackage } from '../entity/result-package';
import { FuncoperationEntity } from '../entity/funcoperationentity';
import { FuncOperationService } from '../service/funcoperation.service';
import { SysManagerUtil } from '../../../common';

@Component({
  selector: 'app-assign-funcop',
  templateUrl: './assign-funcop.component.html',
  styleUrls: ['./assign-funcop.component.css']
})
export class AssignFuncopComponent implements OnInit {

  roleId: string;

  checkedKeys: string[] = [];
  expandedKeys: string[] = ['ERP'];

  opsLoadData: FuncoperationEntity[];
  opsHasAssigned: string[] = [];

  public fetchChildren(node: any): Observable<any[]> {
    return of(node.children);
  }

  public children = (dataItem: any): Observable<any[]> => of(dataItem.children);
  public hasChildren = (dataItem: FuncoperationEntity): boolean => !!dataItem.children && dataItem.children.length > 0;

  constructor(
    private funcopService: FuncOperationService,
    private route: ActivatedRoute,
    private router: Router,
    private sessionmanager: SysManagerUtil
  ) { }

  ngOnInit() {

    this.route.paramMap.subscribe((params: Params) => {
      this.roleId = params.get('id');
    });

    this.funcopService.getFuncopsByRoleId(this.roleId).subscribe(data => {
      data.map(x => {
        this.checkedKeys.push(x);
        this.opsHasAssigned.push(x);
      });
    });

    this.funcopService.getFuncoperations().subscribe(data => {
      this.opsLoadData = data;
      console.log('opsLoadData');
      console.log(this.opsLoadData);
       // 第一层直接展开
       this.opsLoadData.forEach(x => {
        if (isNullOrUndefined(x.parentId)) {
          this.expandedKeys.push(x.id);
        }
      });
    });
    // this.opsLoadData = this.funcopService.getFuncoperations();
  }

  // 弹出框 分配的功能操作控件特性
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'multiple'
    };
  }

  // 弹出框 分配的功能操作控件图标样式
  iconClass({ type }: any): any {
    return {
      'k-i-folder': type.toString() !== '1',
      'k-i-gear': type.toString() === '1',
      'k-icon': true
    };
  }

  save(): void {
    let obj = new ResultPackage();
    obj.removingResults = this.opsHasAssigned.filter(item => this.checkedKeys.indexOf(item) < 0);
    obj.addingResults = this.checkedKeys.filter(item => this.opsHasAssigned.indexOf(item) < 0);
    obj.ownerId = this.roleId;
    this.funcopService.saveFuncopPermission(obj);
    this.opsHasAssigned = [];
    this.checkedKeys.map(x => this.opsHasAssigned.push(x));
  }

  close(): void {
    this.router.navigate([this.sessionmanager.getRoleManagerRouter(), 'roles']);
  }
}



