import { Component, OnInit } from '@angular/core';
import { FuncgroupTreeViewmodel } from './app/role-list.viewmodel';
import { FuncgroupService } from './domain';
import { ActivatedRoute, Params } from '@angular/router';
import { FrameworkService } from '@gsp-sys/rtf-common';
import { SelectableSettings, PagerSettings } from '@progress/kendo-angular-grid';
import { SysManagerUtil } from '../../common';

@Component({
  selector: 'app-role-list',
  templateUrl: './role-list.component.html',
  styleUrls: ['./role-list.component.css']
})
export class FuncgroupTreeComponent implements OnInit {

  public roleList: any[];
  public selectableSettings: SelectableSettings;
  public pageSetting: PagerSettings;
  constructor(
    public viewModel: FuncgroupTreeViewmodel,
    private dataService: FuncgroupService,
    private route: ActivatedRoute,
    private currentSession: SysManagerUtil,
    private frmService: FrameworkService
  ) {

    // 分页模式
    this.pageSetting = {
      buttonCount: 5,
      info: false,
      pageSizes: true,
      previousNext: true
    };
   }

  ngOnInit() {
    this.route.paramMap.subscribe((params: Params) => {
      if (params.has('funcid')) {
        this.currentSession.funcId = params.get('funcid');
        console.log('sessionId:' + this.currentSession.getSessionId());
        // this.currentSession.sessionId = this.frmService.getCurrentSession(this.currentSession.funcId);
      }
    });
    this.viewModel.bindingData.changes.subscribe(
      result => {
        this.roleList = this.viewModel.bindingData.list.toJSON();
      }
    );
    this.viewModel.uiState.selectedIds = [];
    this.viewModel.formLoad();
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'single'
    };

  }
}



