import { Injectable } from '@angular/core';
import { StateMachine, NgStatemachine, State, NgState, RenderState,  NgRenderState, Action, NgAction } from '@farris/devkit';

@Injectable()
@NgStatemachine()
class FuncgroupTreeStatemachine extends StateMachine {

  /**
   * --------------------------------------------------------------------------------
   * 基本状态定义
   * --------------------------------------------------------------------------------
   */


  /**
   * 编辑状态
   */
  @NgState()
  editState: State;

  /**
   * 查看状态
   */
  @NgState({ initialState: true })
  viewState: State;


  /**
   * --------------------------------------------------------------------------------
   * 渲染状态定义
   * --------------------------------------------------------------------------------
   */

  /**
   * 新增按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAdd: RenderState;

  /**
   * 编辑按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'editState'
  })
  canEdit: RenderState;


  /**
   * 删除
   */
  @NgRenderState({
    render: (context) => context.state === 'editState'
  })
  canDelete: RenderState;


  /**
   * 输入控件是否允许输入
   */
  @NgRenderState({
    render: (context) => context.state === 'editState'
  })
  canInput: RenderState;


  /**
   * --------------------------------------------------------------------------------
   * 迁移动作定义
   * --------------------------------------------------------------------------------
   */

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'viewState' })
  view: Action;
}

export { FuncgroupTreeStatemachine };



