import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { RoleListEntity } from './entities/role-list.entity';
import { FuncgroupTreeDatastorage } from './role-list.datastorage';

@Injectable()
@NgRepository({
  from: FuncgroupTreeDatastorage,
  entityType: RoleListEntity
})
class FuncgroupTreeRepository extends Repository<RoleListEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { FuncgroupTreeRepository };



