import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPPosRoleDataStorage } from './gsp-pos-role.datastorage';
import { GSPPositionEntity } from './gspposition.entity';

/**
 * 岗位仓库持久化层
 */
@Injectable()
@NgRepository({
  from: GSPPosRoleDataStorage,
  entityType: GSPPositionEntity
})
export class GSPPosRoleRepository extends Repository<GSPPositionEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}




