import { Provider } from '@angular/core';
import { FuncgroupBindingData } from './role-detail.bindingdata';
import { BaseinfoForm, LimitinfoForm, AuthorizationForm } from './role-detail.form';
import { FuncgroupStateMachine } from './role-detail.statemachine';
import { FuncgroupViewModel } from './role-detail.viewmodel';
import { GSPRoleDetailUIState } from './role-detail.uistate';
import { commandHandlers } from './handler/index';


const APP_PROVIDERS: Provider[] = [
    FuncgroupBindingData,
    BaseinfoForm, LimitinfoForm, AuthorizationForm,
    FuncgroupStateMachine,
    FuncgroupViewModel,
    GSPRoleDetailUIState,

];

export {
    FuncgroupBindingData,
    BaseinfoForm, LimitinfoForm, AuthorizationForm,
    FuncgroupStateMachine,
    FuncgroupViewModel,
    GSPRoleDetailUIState,

    APP_PROVIDERS,
    commandHandlers
};



