import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPPosRoleRepository } from '../domain';
import { GSPPositionEntity } from '../domain/gspposition.entity';

/**
 * 岗位详情绑定数据
 */
@Injectable()
@NgBindingData({
  entity:     GSPPositionEntity,
  repository: GSPPosRoleRepository
})
export class GSPPosRoleBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}




