
import { Routes, RouterModule, Router } from '@angular/router';
import { FuncgroupTreeComponent } from './role-list/role-list.component';
import { FuncgroupComponent } from './role-detail/role-detail.component';
import { AssignFuncopComponent } from './funcoperation/assign-funcop/assign-funcop.component';



const routes: Routes = [
  /**列表页面 */
  { path: 'roles', component: FuncgroupTreeComponent },
  /**详情页面 */
  { path: 'roles/:id', component: FuncgroupComponent },
  { path: 'role-assignop/:id', component: AssignFuncopComponent },
  { path: 'role-detail/new', component: FuncgroupComponent },
  { path: '/:ssid', redirectTo: 'roles' },
  { path: '**', redirectTo: 'roles' }
];

export const GSPRoleManagerRoutingModule = RouterModule.forChild(routes);



