
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { FuncgroupService } from '../../domain/index';
import { FuncgroupTreeStatemachine } from '../role-list.statemachine';
import { FuncgroupTreeUistate } from '../role-list.uistate';

@Injectable()
@NgCommandHandler({
  commandName: 'role-list-formLoad'
})
class FuncgroupFormLoadHandler extends CommandHandler {

  constructor(
    private dataService: FuncgroupService,
    private uiState: FuncgroupTreeUistate,
    private stateMachine: FuncgroupTreeStatemachine
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('loadData', () => {
        this.uiState.action ='load';
        this.dataService.getdata();
      
    });

    // 状态迁移
    // this.addTask('transitState', () => {
    //   const action = this.uiState.action;
    //   this.stateMachine[action]();
    // });
  }
}

export { FuncgroupFormLoadHandler };



