import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPRoleEntity } from './entities/role.entity';
import { FuncgroupDataStorage } from './role-detail.datastorage';

@Injectable()
@NgRepository({
  from: FuncgroupDataStorage,
  entityType: GSPRoleEntity
})
class FuncgroupRepository extends Repository<GSPRoleEntity> {
  constructor(injector: Injector) {
      super(injector);
  }
}

export { FuncgroupRepository };



