import { Provider } from '@angular/compiler/src/core';
import { FuncgroupTreeForm } from './role-list.form';
import { FuncgroupTreeStatemachine } from './role-list.statemachine';
import { FuncgroupTreeUistate } from './role-list.uistate';
import { FuncgroupTreeViewmodel } from './role-list.viewmodel';
import { FuncgroupTreeBindingdata } from './role-list.bindingdata';
import { FuncgroupFormLoadHandler } from './handler/form-load.handler';
import { RouteHandler } from './handler/route.handler';
import { RemoveHandler } from './handler/remove.handler';
import { FormLoadHandler } from '../../role-detail/app/handler/form-load.handler';
import { SaveHandler } from '../../role-detail/app/handler/save.handler';
import { CloseHandler } from '../../role-detail/app/handler/close.handler';
import { SetPosRoleHandler, SavePosRoleHandler, FormLoadPosRoleHandler } from '../../role-detail/position-role/app';

const funcgroupCommandHandler = [
    FuncgroupFormLoadHandler,
    RemoveHandler,
    RouteHandler,
    FormLoadHandler,
    SaveHandler,
    CloseHandler,
    // 授权岗位添加
    SetPosRoleHandler,
    SavePosRoleHandler,
    FormLoadPosRoleHandler
];

const FUNCGROP_PROVIDER: Provider[] = [
    FuncgroupTreeBindingdata,
    FuncgroupTreeViewmodel,
    FuncgroupTreeForm,
    FuncgroupTreeStatemachine,
    FuncgroupTreeUistate
]

export {
    FuncgroupTreeBindingdata,
    FuncgroupTreeViewmodel,
    FuncgroupTreeForm,
    FuncgroupTreeStatemachine,
    FuncgroupTreeUistate,
    FUNCGROP_PROVIDER,
    funcgroupCommandHandler
}



