import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPRoleEntity, FuncgroupRepository } from '../domain/index';

@Injectable()
@NgBindingData({
  entity:     GSPRoleEntity,
  repository: FuncgroupRepository
})
class FuncgroupBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { FuncgroupBindingData };




