import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { FuncgroupTreeBindingdata} from './role-list.bindingdata';

 
@Injectable()
@NgForm({
  bindingData: FuncgroupTreeBindingdata
})
class FuncgroupTreeForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * id
   */
  @NgFormControl({
    name: 'Id',
    binding: 'FID'
  })
  Id: FormControl;

  /**
   * code
   */
  @NgFormControl({
    name: 'code',
    binding: 'FCode'
  })
  Code: FormControl;

  /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'FName'
  })
  Name: FormControl;

  /**
   * 业务对象
   */
  @NgFormControl({
    name: 'path',
    binding: 'FPath'
  })
  Path: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'layer',
    binding: 'FLayer'
  })
  Layer: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'isdetail',
    binding: 'FIsdetail'
  })
  Isdetail: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'roleGroup',
    binding: 'roleGroup'
  })
  roleGroup: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'sortorder',
    binding: 'FSortorder'
  })
  Sortorder: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'sysinit',
    binding: 'FSysinit'
  })
  Sysinit: FormControl;

    /**
   * 业务对象
   */
  @NgFormControl({
    name: 'note',
    binding: 'note'
  })
  note: FormControl;
}


export {FuncgroupTreeForm };



