import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { SysManagerUtil } from '../../../../common';


@Injectable()
@NgCommandHandler({
  commandName: 'close'
})
class CloseHandler extends CommandHandler {

  constructor(
    private sessionmanager: SysManagerUtil,
    private router: Router,
  ) {
    super();
  }

  schedule() {
    this.addTask('close', () => {
      this.router.navigate([this.sessionmanager.getRoleManagerRouter(), 'roles']);
    });
  }
}

export { CloseHandler };



