import { SysManagerUtil } from '../../../../common/util/sys-manager-util';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import { GSPPosRoleUIState } from '../app/gsp-pos-role.uistate';
import { GSPPosRoleRepository } from './gsp-pos-role.repository';
import { ResultPackage } from '../../../../common';



/**功能组岗位关联关系获取岗位数据 */
@Injectable()
export class GSPPosRoleService {

    constructor (
        private rest: RestfulService,
        private posRoleState: GSPPosRoleUIState,
        private sessionUtil: SysManagerUtil,
        private posRoleRep: GSPPosRoleRepository) { }


    /**根据功能组内码获取分配的岗位 */
    getPositionsByRoleId (roleId: string): Observable<any[]> {
        const headers = this.sessionUtil.getHeader();
        const getPosByUserUrl = this.sessionUtil.positionRoleWebApi + '/roles/' + roleId + '/positions';
        return this.rest.get(getPosByUserUrl, null, headers);
    }

    /**修改功能组和岗位的关联关系 */
    modifyPosByRole (): Observable<any> {
        const roleId = this.posRoleState.curRoleId;
        const curPosIdList = this.posRoleRep.getAllEntities().map(item => item.id);
        const posIdBackUpList = this.posRoleState.posIdBackUpList;
        // 获取新增和删除的,这个地方需要新增一个可观察者对象
        const addList = curPosIdList.filter(item => posIdBackUpList.indexOf(item) < 0);
        const removeList = posIdBackUpList.filter(item => curPosIdList.indexOf(item) < 0);
        const posRolePkg = new ResultPackage();
        posRolePkg.ownerId = roleId;
        posRolePkg.addingResults = addList;
        posRolePkg.removingResults = removeList;
        // 无需走请求
        if (addList.length === 0 && removeList.length === 0) {
            return new Observable(observer => observer.next());
        }
        console.log('add');
        console.log(addList);
        console.log('remove');
        console.log(removeList);
        const headers = this.sessionUtil.getHeader();
        const posRoleWebUrl = this.sessionUtil.positionRoleWebApi;
        const queryParams = {
            subjectType: 'role',
            objectType: 'position'
        };
        return this.rest.post(posRoleWebUrl, posRolePkg, queryParams, headers)
            .map(item => {
                this.posRoleState.posIdBackUpList = curPosIdList;
                return item;
            });
    }

    /**获取所有岗位 */
    getAllPositions (): Observable<any> {
        const headers = this.sessionUtil.getHeader();
        const posUrl = this.sessionUtil.positionWebApi + '/all';
        return this.rest.get(posUrl, null, headers);
    }
}



