
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { FuncgroupTreeBindingdata } from '../role-list.bindingdata';
import { FuncgroupTreeUistate } from '../role-list.uistate';
import { SysManagerUtil } from '../../../../common';

@Injectable()
@NgCommandHandler({
  commandName: 'route'
})
class RouteHandler extends CommandHandler {

  constructor(
    private sessionmanager: SysManagerUtil,
    private router: Router,
    private bindingData: FuncgroupTreeBindingdata,
    private uiState: FuncgroupTreeUistate
  ) {
    super();
  }

  schedule() {
    this.addTask('route', (lastResult: any, context: TaskExecutionContext) => {
      const command = context.command;
      const action = command.params.action;
      const dataIds = this.uiState.selectedIds;
      if (action === 'edit' || action === 'assignOp') {
        if (dataIds.length === 0) {
          alert('请选择要编辑的功能组！');
          return;
        }
        if (dataIds.length > 1) {
          alert('请选择一个功能组！');
          return;
        }
      }

      if (action === 'assignOp') {
        this.router.navigate([this.sessionmanager.getRoleManagerRouter() , 'role-assignop', dataIds[0]]);
        return;
      }

      const id = (action === 'add') ? 'new' : dataIds[0];
      this.router.navigate([this.sessionmanager.getRoleManagerRouter() , 'roles', id]);
    });
  }
}

export { RouteHandler };



